<template>
    <div class="container">
       <div class="main__cars" v-if="users">
        <div class="main__cars-item" v-for="user in users" :key="user.id">
            <router-link :to="/users/ + user.id">{{ user.name }}</router-link>
            <p>Email: {{ user.email }}</p>
            <p>City: {{ user.address.city }}</p>
        </div>
       </div>
       <div v-else>
        Идет загрузка...
       </div>
    </div>
</template>

<script>

    export default {
        created() {
            this.getUsers()
        },
        methods: {
            getUsers() {
                this.$store.dispatch('getUsers')
            },
        },
        computed:{
            users(){
                return this.$store.state.users
            }
        }
    }
</script>

